function createCharts (cf) {

  mapboxgl.accessToken = "pk.eyJ1IjoibWFwZCIsImEiOiJjaWV1a2Zza2IwaXY5dThtMDU2bm5xdTQ1In0.U7vvTzg4HBgUGAB_sy0xhw"
  const map = new mapboxgl.Map({
    container: "heatmap",
    style: MAP_STYLE,
    center: [-74.50, 40],
    zoom: 1
  })

  const state = {
    mark: "hex",
    encoding: {
      x: {
        type: "quantitative",
        field: "lon",
        size: WIDTH
      },
      y: {
        type: "quantitative",
        field: "lat",
        size: HEIGHT
      },
      color: {
        type: "quantize",
        aggregate: "count(lang)",
        scale: {
          domain: "auto",
          range: colorRange,
          default: "#0d0887",
          nullValue: "#0d0887"
        }
      },
      size: {
        type: "manual",
        value: 10
      }
    }
  }

  const xDim = cf.dimension("lon")
  const yDim = cf.dimension("lat")

  const data = simpleHeatmapLayer()
    .setConfig({
      crossfilter: cf,
      connector: Connector,
      xDimension: xDim,
      yDimension: yDim,
      height: HEIGHT,
      width: WIDTH
    })
    .setState(state)
    .getData((error, image) => {
      const blobUrl = "data:image/png;base64," + image
      console.log(blobUrl)

      const dummy = "data:image/jpeg;base64,"

      const layerID = "heatmap-layer"
      map.addSource(layerID, {
        type: "image",
        url: dummy,
        coordinates: [
            [-80.425, 46.437],
            [-71.516, 46.437],
            [-71.516, 37.936],
            [-80.425, 37.936]
        ]
        // url: blobUrl,
        // coordinates: [[-90, -90], [90, -90], [90, 90], [-90, 90]]
      })

      map.addLayer({
        id: layerID,
        source: layerID,
        type: "raster",
        paint: {"raster-opacity": 0.5, "raster-fade-duration": 0}
      })
    })


  // const RasterChart = rasterChart(cf)
  // countWidget(cf)
  // return RasterChart.init().then(() => dc.renderAllAsync())
}

document.addEventListener("DOMContentLoaded", function init() {
  return connect()
    .then(createCrossfilter)
    .then(createCharts)
})



